import React, { useState, useEffect } from 'react'
import Header from './Header'
import Footer from './Footer'
import Note from './Note'
import CreateArea from './CreateArea'
import LoginPage from './LoginPage'
import SignupPage from './SignupPage'

function App() {
    const [isLogin, setIsLogin] = useState({
        isAuth: false,
        token: null,
        userId: null,
        error: null,
        msg: ""
    })

    const [posts, setPosts] = useState({
        posts: [],
        error: ''
    })
    useEffect(() => {

        const token = localStorage.getItem('token');
        const expiryDate = localStorage.getItem('expiryDate')
        if (!token || !expiryDate) {
            return
        }
        if (new Date(expiryDate) <= new Date()) {
            logoutHandler()
            return
        }
        const userId = localStorage.getItem('userId');
        const remainingMilliseconds = new Date(expiryDate).getTime() - new Date().getTime();
        setIsLogin({
            isAuth: true,
            token: token,
            userId: userId
        })
        loadPosts()
        setAutoLogout(remainingMilliseconds)
    }, []);

    function setAutoLogout(milliseconds) {
        setTimeout(() => {
            logoutHandler();
        }, milliseconds);
    };

    function logoutHandler() {
        setIsLogin({
            isAuth: false,
            token: null
        })

        localStorage.removeItem('token');
        localStorage.removeItem('expiryDate');
        localStorage.removeItem('userId');
    };

    function loginHandler(event, authData) {
        event.preventDefault()
        loadPosts()
        fetch('https://authapi-264513.appspot.com/auth/login', {
        // fetch('http://localhost:8080/auth/login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                email: authData.email,
                password: authData.password
            })
        })
            .then((res) => {
                if (res.status === 422) {
                    throw new Error('Validation failed')
                }
                if (res.status !== 200 && res.status !== 201) {
                    throw new Error('Could not authenticate you')
                }
                return res.json()
            }).then(resData => {
                setIsLogin({
                    isAuth: true,
                    token: resData.token,
                    userId: resData.user._id
                })

                localStorage.setItem('token', resData.token)
                localStorage.setItem('userId', resData.user._id)

                const remainingMilliseconds = 60 * 60 * 1000;
                const expiryDate = new Date(
                    new Date().getTime() + remainingMilliseconds
                );
                localStorage.setItem('expiryDate', expiryDate.toISOString());
                setAutoLogout(remainingMilliseconds);

            }).catch((err) => {
                setIsLogin({ isAuth: false })
            });
    }

    function signupHandler(event, authData) {
        event.preventDefault()
        fetch('https://authapi-264513.appspot.com/auth/signup', {
        // fetch('http://localhost:8080/auth/signup', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                name: authData.name,
                email: authData.email,
                password: authData.password
            })
        }).then(res => {
            if (res.status === 422) {
                throw new Error(
                    "Validation failed. Make sure the email address isn't used yet!"
                );
            }
            if (res.status !== 200 && res.status !== 201) {
                console.log('Error!');
                throw new Error('Creating a user failed!');
            }
            return res.json();
        }).then(resData => {
            setIsLogin({
                msg: "SignUp Success"
            })
        }).catch(err => {
            setIsLogin({ 
                msg: "Error"
            })
        });
    }

    function loadPosts() {
        fetch('http://https://authapi-264513.appspot.com')
        // fetch('http://localhost:8080')
            .then((res) => {
                if (res.status !== 200) {
                    throw new Error('Failed to fetch posts')
                }
                return res.json()
            }).then(resData => {
                console.log("resData =" + resData.posts)
                setPosts({
                    posts: resData.posts
                })
            })
            .catch((err) => {
                console.log('err=' + err)
                setPosts({
                    error: err
                })
            });
    }


    return <div>
        <Header isLogin={isLogin.isAuth} onLogout={logoutHandler} />
        <p>{isLogin.msg}</p>
        {isLogin.isAuth && <CreateArea loadPost={loadPosts} token={isLogin.token} />}
        {!isLogin.isAuth &&
            (
                <div>
                    <LoginPage onLogin={loginHandler} />
                    <SignupPage onSignup={signupHandler} />
                </div>
            )
        }
        {posts.posts.map((postItem) => (
            <Note
                loadPost={loadPosts}
                key={postItem._id}
                id={postItem._id}
                name={postItem.name}
                description={postItem.description}
                token={isLogin.token}
            />
        ))}
        <Footer />
    </div>
}

export default App